
class Bluetooth {

	constructor() {
		this.isOpenBle = false;
		this.deviceId = "";
		this.serviceId = "";
		this.writeId = "";
		this.notifyId = "";
		this.openBluetoothAdapter();
	}

	showToast(title) {
		uni.showToast({
			title: title,
			icon: 'none',
			'duration': 2000
		});
	}

	openBluetoothAdapter() {
		return new Promise((resolve, reject) => {
			uni.openBluetoothAdapter({
				success: res => {
					this.isOpenBle = true;
					// this.showToast("");
					resolve(res);
				},
				fail: err => {
					this.showToast(`` + JSON.stringify(err));
					reject(err);
				},
			});
		});

	}

	startBluetoothDevicesDiscovery() {
		if (!this.isOpenBle) {
			this.showToast(``)
			return;
		}

		let self = this;
		// uni.showLoading({
		// 	title: ''
		// })
		return new Promise((resolve, reject) => {
			setTimeout(() => {
				uni.startBluetoothDevicesDiscovery({
					success: res => {
						resolve(res)
					},
					fail: res => {
						self.showToast(`` + JSON.stringify(err));
						reject(err);
					}
				})
			}, 300);
		});
	}

	stopBluetoothDevicesDiscovery() {
		let self = this;
		return new Promise((resolve, reject) => {
			uni.stopBluetoothDevicesDiscovery({
				success: e => {
					uni.hideLoading();
				},
				fail: e => {
					uni.hideLoading();
					console.log(`` + JSON.stringify(err))
					// self.showToast(`` + JSON.stringify(err));
				}
			})
		});
	}

	createBLEConnection() {
		//deviceId
		let deviceId = this.deviceId;
		let self = this;

		// uni.showLoading({
		// 	mask: true,
		// 	title: ',...'
		// })
		console.log(this.deviceId);
		return new Promise((resolve, reject) => {
			uni.createBLEConnection({
				deviceId,
				success: (res) => {
					console.log("res:createBLEConnection " + JSON.stringify(res));
					resolve(res)
				},
				fail: err => {
					uni.hideLoading();
					console.log(`222` + JSON.stringify(err))
					// ,
					if(err.errCode != -1){
						self.showToast(`` + JSON.stringify(err));
					}
					reject(err);
				}
			})
		});
	}

	//(service)
	getBLEDeviceServices() {
		let _serviceList = [];
		let deviceId = this.deviceId;
		let self = this;
		return new Promise((resolve, reject) => {
			setTimeout(() => {
				uni.getBLEDeviceServices({
					deviceId,
					success: res => {
						console.log(res)
						for (let service of res.services) {
							if (service.isPrimary) {
								_serviceList.push(service);
							}
						}
						uni.hideLoading();
						console.log("_serviceList: " + JSON.stringify(_serviceList));
						resolve(_serviceList)
					},
					fail: err => {
						uni.hideLoading();
						self.showToast(`Services` + JSON.stringify(err));
						reject(err);
					},
				})
			}, 1500);
		});
	}

	//(characteristic)
	getBLEDeviceCharacteristics() {
		let deviceId = this.deviceId;
		let serviceId = this.serviceId;

		let self = this;
		return new Promise((resolve, reject) => {
			uni.getBLEDeviceCharacteristics({
				deviceId,
				serviceId,
				success: res => {
					for (let _obj of res.characteristics) {
						//notify
						if (_obj.properties.notify) {
							self.notifyId = _obj.uuid;
							uni.setStorageSync('notifyId', self.notifyId);
						}
						//writeId
						if (_obj.properties.write) {
							self.writeId = _obj.uuid;
							uni.setStorageSync('writeId', self.writeId);
						}
					}

					//console.log("res:getBLEDeviceCharacteristics " + JSON.stringify(res));
					let result = {
						'notifyId': self.notifyId,
						'writeId': self.writeId
					};
					// self.showToast(`OK,${JSON.stringify(result)}`);
					resolve(result)
				},
				fail: err => {
					self.showToast(`getBLEDeviceCharacteristics` + JSON.stringify(err));
					reject(err);
				}
			})
		});
	}

	//
	closeBLEConnection() {
		let deviceId = this.deviceId;
		uni.closeBLEConnection({
			deviceId,
			success(res) {
				console.log(res)
			}
		})
	}

	notifyBLECharacteristicValue() {
		let deviceId = this.deviceId;
		let serviceId = this.serviceId;
		let characteristicId = this.notifyId;
		console.log(deviceId,serviceId,characteristicId,99999)
		uni.notifyBLECharacteristicValueChange({
			state: true, //  notify 
			deviceId,
			serviceId,
			characteristicId,
			success(res) {
				uni.onBLECharacteristicValueChange(function(res) {
					console.log(res)
				});
			},
			fail(res) {
				console.log('notifyBLECharacteristicValueChange failed:' + res.errMsg);

			}
		});
	}

	writeBLECharacteristicValue(buffer) {
		let deviceId = this.deviceId;
		let serviceId = this.serviceId;
		let characteristicId = this.writeId;

		console.log("this: " + JSON.stringify(this));
		
		return new Promise((resolve, reject) => {
			uni.writeBLECharacteristicValue({
				deviceId,
				serviceId,
				characteristicId,
				value: buffer,
				success(res) {
					console.log('message', JSON.stringify(res));
					resolve(res);
				},
				fail(err) {
					console.log('message', JSON.stringify(err));
					reject(err);
				}
			});
		});
	}

	closeBluetoothAdapter() {
		uni.closeBluetoothAdapter({
			success: res => {
				console.log(res)
			}
		});
	}

	//APP deviceId 
	reconnect() {
		(async () => {
			try {
					this.deviceId = this.deviceId || uni.getStorageSync("deviceId");
					this.serviceId = this.serviceId || uni.getStorageSync("serviceId");
					
					let result1 = await this.createBLEConnection();
					// console.log("createBLEConnection: " + JSON.stringify(result1));
					
					let result2 = await this.getBLEDeviceServices();
					// console.log("getBLEDeviceServices: " + JSON.stringify(result2));
					
					let result3 = await this.getBLEDeviceCharacteristics();
					console.log('')
					// console.log("getBLEDeviceCharacteristics: " + JSON.stringify(result3));
					// ,queryAnalysisdata
					// let result4 = await this.notifyBLECharacteristicValue();
				
				
				// this.writeId = uni.getStorageSync("writeId");
				// this.notifyId = uni.getStorageSync("notifyId");
			} catch (err) {
				// ,,
				if(err.errCode == -1){
					let result2 = await this.getBLEDeviceServices();
					// console.log("getBLEDeviceServices: " + JSON.stringify(result2));
					
					let result3 = await this.getBLEDeviceCharacteristics();
				}
			}

		})();
	}
}

export default Bluetooth;
